.header{
	&.is-sticky{
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		z-index: 9;
	}
	.navigation-menu{
		padding-top: 12px;
		padding-bottom: 12px;
		display: flex;
		justify-content: space-between;
		ul{
			list-style-type: none;
			padding-left: 0;
			margin-bottom: 0;
			li{
				display: inline-block;
				&:not(:last-child){
					margin-right: 32px;
				}
				&.logo{
					$logo-size: 60px;
					img{
						width: $logo-size;
						height: $logo-size;
					}
				}
				a{
					color: $brand;
					font-weight: 600;
					transition: border .3s ease, color .3s ease;
					text-decoration: none;
					border-bottom: 3px solid transparent;
					&:hover{
						color: $brand;
						border-color: $brand;
					}
				}
			}
			&.social-list{

			}
		}
		&-right{

		}
	}
}